import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router-dom';
import { MDBBtn, MDBCol, MDBContainer, MDBInput, MDBRow } from 'mdbreact';
import * as actions from '../../store/actions/index';

class AuthPage extends Component {
  state = {
    username: "",
    password: "",
    isSignup: false
  };

  componentDidMount() {
    if (this.props.authRedirectPath !== '/dashboard') {
        this.props.onSetAuthRedirectPath();
    }
  };

  submitHandler = event => {
    event.preventDefault();
    const { username, password, isSignup } = this.state;
    this.props.onAuth( username, password, isSignup );
  };
    
  changeHandler = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

    render() {
      let authRedirect = null;
      if (this.props.isAuthenticated) {
        authRedirect = <Redirect to={this.props.authRedirectPath}/>
      }
      
      return (
        <div>
          {authRedirect}
          <MDBContainer>
            <MDBRow className="d-flex justify-content-center">
              <MDBCol md="6">
                <form onSubmit={this.submitHandler}>
                  <p className="h5 text-center mb-4">Login</p>
                    <div className="grey-text">
                      <MDBInput
                        className="form-control"
                        type="text"
                        name="username"
                        id="username"
                        value={this.state.username}
                        onChange={this.changeHandler}
                        label="Your username"
                        icon="user"
                        required
                      />
                      <MDBInput
                        className="form-control"
                        type="password"
                        name="password"
                        id="password"
                        value={this.state.password}
                        onChange={this.changeHandler}
                        label="Your password"
                        icon="lock"
                        required
                      />
                      <small className="form-text text-muted">
                        &emsp;&emsp;&emsp;&nbsp;Password must be at least 6 characters long, contain letters and digits.
                      </small>
                      <br />
                      <p className="grey-text d-flex justify-content-end">
                        Forgot
                          <a href="#!" className="dark-grey-text ml-2">Password?</a>
                      </p>
                      <MDBCol md="4" className="mb-3">
                        <div className="custom-control custom-checkbox pl-3">
                          <input
                            className="custom-control-input"
                            type="checkbox"
                            value=""
                            id="remeberme"
                          />
                          <label className="custom-control-label" htmlFor="remeberme">
                            Remember me
                          </label>
                        </div>
                      </MDBCol>
                    </div>
                    <div className="text-center">
                      <MDBBtn type="submit">Login</MDBBtn>
                    </div>
                    </form>
                    <br />
                    <p className="grey-text d-flex justify-content-center">
                      Don't have an account?
                      <Link to="register" className="dark-grey-text ml-2">Register</Link>
                    </p>
                </MDBCol>
              </MDBRow>
            </MDBContainer>
          </div>
        );
      }
    };

const mapStateToProps = state => {
  return {
    error: state.error,
    isAuthenticated: state.isAuthenticated,
    authRedirectPath: state.authRedirectPath
  };
};

const mapDispatchToProps = dispatch => {
  return {
      onAuth: (username, password, isSignup) => dispatch(actions.auth(username, password, isSignup)),
      onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath('/dashboard'))
    };
};

export default connect( mapStateToProps, mapDispatchToProps )( AuthPage );